import react, {Component} from 'react';
import Layout from '../../components/layout'
import {Message} from 'bloomer'
import { Container, Title} from 'bloomer'
import web3 from '../../ethereum/web3'


export default class Dapps extends Component {
    state ={
        metamask: '',
        message: false,
        errorMessage: ''

    }
    // static async getInitialProps() {
    //     console.log("checking web3")
    
    //   return {metamask: web3}

    // }
    checkMetamask(){
     return <Message content="i am here"/>
   
    }
    render() {
        console.log("what is in ", this.props.metamask)
        return ( <Layout>

            <Container>
            <Title isSize = {4}> The playground page </Title>
             <p> Ethereum playground </p>
             {this.checkMetamask()}
            
             
              </Container> </Layout>)
        }

    }


